import React, { useState } from 'react';
import FaqContent from './faqContent/FaqContent';
import s from './FaqSection.module.scss';






const FaqSection = () => {
    const [curState, setState] = useState(0);
    const dataFaq = [
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
    ]

    const data = {
        title:  "    ?",
        title2: "    ?2",
        title3: "    ?3",
        title4: "    ?4",
        title5: "    ?5",
        title6: "    ?6",

        text: "       ,       .        ,       .",
        text2: "      ,     :  ,  , ,         .         :                         .2",
        text3: "      ,     :  ,  , ,         .         :                         .3",
        text4: "      ,     :  ,  , ,         .         :                         .4",
        text5: "      ,     :  ,  , ,         .         :                         .5",
        text6: "       ,       .        ,       .6",
        
    }



    return (
        <section className={s.faqSection}>
            <div className='container'>
                <div className={s.faqSection__inner}>
                    <div className={s.faqSection__tabs}>
                        <div className={s.faqSection__tabsContentLeft}>
                            <div className={s.faqSection__logo}>
                                <img src={process.env.PUBLIC_URL + "/img/Planometrika-logo-white.svg"} alt="" />
                            </div>
                            <div className={s.faqSection__tabsContent}>
                                {{
                                    0: <FaqContent title={data.title} text={data.text}/>,
                                    1: <FaqContent title={data.title2} text={data.text2} />,
                                    2: <FaqContent title={data.title3} text={data.text3} />,
                                    3: <FaqContent title={data.title4} text={data.text4} />,
                                    4: <FaqContent title={data.title5} text={data.text5} />,
                                    5: <FaqContent title={data.title6} text={data.text6} />,
                                }[curState]}
                            </div>
                        </div>
                        <div className={s.faqSection__tabBtns}>
                            {dataFaq.map((dataFaq, index) =>
                                <div className={curState === index ? "button active" : "button"} key={index} onClick={() => setState(index)}>
                                    <div>{dataFaq.title}</div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default FaqSection;