import React, {useEffect, useState} from 'react';
import { Button } from "antd";
import { RequestSteps } from "./RequestSteps";
import s from './RequestSteps.module.scss';
import { Upload } from 'antd';
import { Checkbox } from 'antd';
import UserAbout from './requestComponents/userAbout/UserAbout';
import InfoSteps from './requestComponents/infoSteps/InfoSteps';
import {useApi} from "../../hooks/useApi";
import {getData} from "../../utils/utils";
import {hostName} from "../../API/config";

const Plan = ({ nextStep, form, setForm }) => {
    const { Dragger } = Upload;
    const [stepComplete, setStepComplete] = useState(false)
	const [orderFiles, setOrderFiles] = useState([])

    const [saveAll, saveAllIsLoading] = useApi({
        url: '/orders/create',
        headers: {
            //'Content-Type': 'multipart/form-data'
            'Content-Type': 'application/json'
        },
        data: getData(form, orderFiles)
    });

    const props = {
        name: 'file',
        multiple: true,
        action: hostName + '/orders/addFiles',
        headers: {
            'Authorization': localStorage.getItem('token')
        },
        onChange(info) {
            const { status } = info.file;

            if (status !== 'uploading') {
                setForm({...form, files: [...form.files, info.file] })
                setStepComplete(true)
                console.log(info.file, info.fileList);
            }

            if (status === 'done') {
                console.log(`${info.file.name} file uploaded successfully.`);
                console.log(info.file);
				setOrderFiles([...orderFiles, { id: info.file.response[0].id }])
            } else if (status === 'error') {
                console.log(`${info.file.name} file upload failed.`);
            }
        },
        onDrop(e) {
            console.log('Dropped files', e.dataTransfer.files);
        },
    };

    const data = {
        numberStep: "1",
        title: " ",
        par1: "1.        : ,      ,    ;        .        ;",
        par2: "2. ,     ,   ;        . 1.       ;",
    }

    const dummyRequest = ({ file, onSuccess }) => {
        setTimeout(() => {
            onSuccess("ok");
        }, 0);
    };

    const saveRequest = () => {
        saveAll().then((resp) => {
			setForm({...form, order: {id: resp.id}})
        	nextStep(RequestSteps.RATE)
		}).catch((e) => console.log(e.message))
    }

    useEffect(() => {
        console.log(form);
    },[])

    return (
        <div className={s.stepPlan}>
            <div className="container">
                <div className={s.inner}>
                    <div className={s.quizeBlock}>
                        <div className={s.title}>  &nbsp;  </div>
                        <div className={s.done}>
                            <img src="img/done2.svg" alt="" />
                            <span> </span>
                        </div>
                        <div className={s.done}>
                            <img src="img/done2.svg" alt="" />
                            <span></span>
                        </div>
                        <div className={s.aboutQuize}>
                            <div className={s.aboutquizeTitle}> </div>
                            <div className={s.aboutquizeSubtitle}>
                                     , 
                                   ,
                                    
                                   
                            </div>
                        </div>
                        <div className={s.upLoadWrap}>
                            <Dragger {...props} listType="picture">
                                <p className="ant-upload-hint">
                                         pdf 
                                </p>
                                <p className="ant-upload-text">
                                    <img src="img/upLoad.svg" alt="" />
                                       </p>
                            </Dragger>
                        </div>
                        <Checkbox style={{ marginBottom: '4rem' }}>
                                ,    
                        </Checkbox>
                        <div className="nextstep-wrap">
                            <Button className={s.btnColor} type="primary" loading={saveAllIsLoading} onClick={() => saveRequest()} disabled={!stepComplete}></Button>
                            <span className={'fade ' + (stepComplete && 'hide')}>   </span>
                        </div>
                    </div>
                    <div className={s.infoBlock}>
                        <UserAbout user={form.user} modal={form} setUser={(val) => setForm({...form, user: val})}/>
                        <InfoSteps numberStep={data.numberStep} title={data.title} par1={data.par1} par2={data.par2} />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Plan;
