
import React from "react";
import s from "./ContactsBlock.module.scss";



const ContactsBlock = () => {
    const [visible, setVisible] = React.useState(true);

    const handleClose = () => {
        setVisible(false);
    };

    return (
        <>
            {visible ? (
                <div className={s.contacts}>
                    <div className={s.top}>
                        <div className={s.logo}>
                            <img src="img/logoMedium.png" alt="" />
                        </div>
                        <div className={s.blockTitle}></div>
                        <button className={s.blockClose} onClick={() => handleClose()}>
                            <img src="img/clouseIcon.svg" alt="" />
                        </button>
                    </div>
                    <div className={s.info}>
                        <div className={s.infoItem}>
                            <div className={s.key}></div>
                            <div className={s.value}>. ,    .9 .1</div>
                        </div>
                        <div className={s.infoItem}>
                            <div className={s.key}></div>
                            <a href="tel:+89959044150" className={s.value}>8 (995) 904-41-50</a>
                        </div>
                        <div className={s.infoItem}>
                            <div className={s.key}></div>
                            <div className={s.value}>-: 10-20 / -: 10-15</div>
                        </div>
                        <div className={s.infoItem}>
                            <div className={s.key}></div>
                            <div className={s.value}></div>
                        </div>
                        <div className={s.infoItem}>
                            <div className={s.key}></div>
                            <div className={s.value}> 771579800634 :&nbsp;313774619000634</div>
                        </div>
                    </div>
                    <div className={s.bottom}>
                        <div className={s.bottomTitle}> </div>
                        <div className={s.bottomInfo}>  PLANOMETRIKA      </div>
                    </div>
                </div>
            ) : null}
        </>
    );
};


export default ContactsBlock;



