import React from "react";
import { Collapse } from 'antd';




const Accordion = () => {
const { Panel } = Collapse;


    return (
        <Collapse bordered={false} defaultActiveKey={['1']}>
            <Panel header="        ?" key="1">
                      ,   
                    .
                        .
                      ,     
                  .      ,
                     
            </Panel>
            <Panel header="        ?" key="2">
                      ,   
                    .
            </Panel>
            <Panel header="        ?" key="3">
                      ,   
                    .
            </Panel>
        </Collapse>

    );

};


export default Accordion;

