import React, {useEffect, useState} from 'react';
import { RequestSteps } from "./RequestSteps";
import { Button } from "antd";
import s from './RequestSteps.module.scss';
import UserAbout from './requestComponents/userAbout/UserAbout';
import InfoSteps from './requestComponents/infoSteps/InfoSteps';
import FormQuest from "./FormQuest";

const Questionnaire = ({ nextStep, form, setForm }) => {
	console.log('quest')
    const [stepComplete, setStepComplete] = useState(false)
    const data = {
        numberStep: "1",
        title: " ",
        par1: "1.        : ,      ,    ;        .        ;",
        par2: "2. ,     ,   ;        . 1.       ;",
    }

    useEffect(() => {
        const validate = () => {
            setStepComplete(
                form.questionnaire.tenantsCount &&
                form.questionnaire.pet &&
                (form.questionnaire.pet === '' ? form.questionnaire.petAdvanced : true) &&
                form.questionnaire.age &&
                form.questionnaire.childrens &&
                (form.questionnaire.childrens === '' ? true : form.questionnaire.childrensCount &&
                    (form.questionnaire.childrens !== '' ? true : form.questionnaire.childrensAge )) &&
                form.questionnaire.guests &&
                form.questionnaire.guestsCount
            )
        }
        validate();
    },[form])

    return (
        <section className={s.step1}>
            <div className="container">
                <div className={s.inner}>
                    <div className={s.quizeBlock}>
                        <div className={s.title}>  &nbsp;  </div>
                        <div className={s.aboutQuize}>
                            <div className={s.aboutquizeTitle}> </div>
                            <div className={s.aboutquizeSubtitle}>
                                       
                                       !
                            </div>
                        </div>
                        <FormQuest form={form} setForm={setForm}/>
                        <div className={s.next}>
                            <div className={s.nextItem}></div>
                            <div className={s.nextItem}> </div>
                        </div>
                        <div className="nextstep-wrap">
                            <Button className={s.btnColor} type="primary" onClick={() => nextStep(RequestSteps.CHARACTERISTICS)} disabled={!stepComplete}></Button>
                            <span className={'fade ' + (stepComplete && 'hide')}>    </span>
                        </div>
                    </div>
                    <div className={s.infoBlock}>
                        <UserAbout user={form.user} modal={form} setUser={(val) => setForm({...form, user: val})}/>
                        <InfoSteps numberStep={data.numberStep} title={data.title} par1={data.par1} par2={data.par2} />
                    </div>
                </div>
            </div>
        </section >
    );
};

export default Questionnaire;
