import React from 'react';
import { RequestSteps } from "./RequestSteps";
import LoginForm from '../loginForm/LoginForm';
import s from './RequestSteps.module.scss';
import MyBtn from '../myBtn/MyBtn';
import {Link} from "react-router-dom";
import {Button} from "antd";




const Registration = ({ nextStep }) => {
    return (
        <section className={s.login}>
            <div className="container">
                <div className={s.inner}>
                    <div className={s.loginBlock}>
                        <LoginForm>
                            <MyBtn title="" />
                            <Button className={s.btnDark} type="primary"
                            onClick={() => nextStep(RequestSteps.QUESTIONNAIRE)}></Button>
                        </LoginForm>
                        <div className={s.loginSocial}>
                            <div className={s.socialName}>   </div>
                            <Link to="#" className={s.socialIcon}>
                                <img src="img/fbIcon.svg" alt="" />
                            </Link>
                            <Link to="#" className={s.socialIcon}>
                                <img src="img/vkIcon.svg" alt="" />
                            </Link>
                        </div>
                    </div>
                    <div className={s.infoBlock}>
                        <div className={s.infoItem}>
                            <div className={s.titleItem}>      !</div>
                            <div className={s.subtitleItem}>    
                                ,     .
                                       ,
                                       .
                                        ,
                                  .
                                          
                                        ,
                                , , ,    , 
                                  .
                            </div>
                        </div>
                        <div className={s.infoItem}>
                            <div className={s.titleItem}>  24 !</div>
                            <div className={s.subtitleItem}>     
                                         ,
                                 ,      .
                                     
                                     ,    
                                  .
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Registration;