import React from 'react';
import { RequestSteps } from "./RequestSteps";
import { Button, Modal, Collapse, Select, Checkbox,  message, Upload} from "antd";
import s from './RequestSteps.module.scss';
import UserAbout from './requestComponents/userAbout/UserAbout';
import InfoSteps from './requestComponents/infoSteps/InfoSteps';
import Time from './requestComponents/time/Time';

const { Option } = Select;
const { Panel } = Collapse;
const { Dragger } = Upload;

const props = {
    name: 'file',
    multiple: true,
    action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',

    onChange(info) {
        const { status } = info.file;

        if (status !== 'uploading') {
            console.log(info.file, info.fileList);
        }

        if (status === 'done') {
            message.success(`${info.file.name} file uploaded successfully.`);
        } else if (status === 'error') {
            message.error(`${info.file.name} file upload failed.`);
        }
    },

    onDrop(e) {
        console.log('Dropped files', e.dataTransfer.files);
    },
};

const Waiting = ({ hours = 0, minutes = 0, seconds = 10, nextStep, form, setForm }) => {
    const [valuePet, setValuePet] = React.useState('');
    const [valueChildren, setValueChildren] = React.useState('');
    const [checked1, setChecked1] = React.useState(false);
    const [checked2, setChecked2] = React.useState(false);
    const [checked3, setChecked3] = React.useState(false);
    const [checked4, setChecked4] = React.useState(false);
    const [checked5, setChecked5] = React.useState(false);
    const [checked6, setChecked6] = React.useState(false);
    const [checked7, setChecked7] = React.useState(false);
    const [checked8, setChecked8] = React.useState(false);
    const [isModalVisible, setIsModalVisible] = React.useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };


    const [over, setOver] = React.useState(false);
    const [[h, m, sec], setTime] = React.useState([hours, minutes, seconds]);

    const tick = () => {

        if (h === 0 && m === 0 && sec === 0) {
            setOver(true);
            console.log(' ')
        } else if (m === 0 && sec === 0) {
            setTime([h - 1, 59, 59]);
        } else if (sec === 0) {
            setTime([h, m - 1, 59]);
        } else {
            setTime([h, m, sec - 1]);
        }
    };

    React.useEffect(() => {
        const timerID = setInterval(() => tick(), 1000);
        return () => clearInterval(timerID);
    });

  

    const data = {
        numberStep: "4",
        title: "  ",
        par1: "      ,     :  ,  , ,         .         :                         .",
        par2: "         24         PDF:          ,  ,     ,       .",
    }

    return (
        <div className={s.waiting}>
            <div className="container">
                <div className={s.inner}>
                    <div className={s.quizeBlock}>
                        <div className={s.title}>     </div>
                        <Time hours={0} minutes={0} seconds={10}>
                            <Button className={over ? "BtnColorTime active" : 'BtnColorTime'} onClick={() => nextStep(RequestSteps.RESULT)} type="primary"> </Button>
                        </Time>

                    </div>
                    <div className={s.infoBlock}>
                        <UserAbout user={form.user} setUser={(val) => setForm({...form, user: val})} modal={form} setModal={setForm}/>
                        <InfoSteps numberStep={data.numberStep} title={data.title} par1={data.par1} par2={data.par2} />
                    </div>

                    <Modal className='modalAnket' visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                        <div className={s.contentWrap}>
                            <div className='title-anket'> </div>
                            {/* <div className={s.subtitle}>   </div> */}

                            <Collapse accordion className='anket'>
                                <Panel className='anket' header=" " key="1">
                                    <div className="item-subtitle">
                                               
                                               !
                                    </div>
                                    <div className={s.itemsQuize}>
                                        <div className={s.item}>
                                            <div className={s.selectTitle}>1.       ?</div>
                                            <div className={s.selectItem}>
                                                <div className={s.selectSubtitle}>-</div>
                                                <Select className={s.select}>
                                                    <Option value="" hidden></Option>
                                                    <Option value="1">1</Option>
                                                    <Option value="2">2</Option>
                                                    <Option value="3">3</Option>
                                                    <Option value="4">4</Option>
                                                    <Option value="5+">5+</Option>
                                                </Select>
                                            </div>
                                        </div>
                                        <div className={s.item}>
                                            <div className={s.selectTitle}>2.      ?</div>
                                            <div className={s.selectesWrap}>
                                                <div className={s.selectItem}>
                                                    <div className={s.selectSubtitle}></div>
                                                    <Select className={s.select}
                                                        onChange={setValuePet}> value={valuePet}
                                                        <Option value="" hidden></Option>
                                                        <Option value=""></Option>
                                                        <Option value=""></Option>
                                                        <Option value=""></Option>
                                                    </Select>
                                                </div>
                                                <div className={s.selectItem}>
                                                    {valuePet === '' &&
                                                        <div>
                                                            <div className={s.selectSubtitle}></div>
                                                            <input className={s.input} type="text" />
                                                        </div>}
                                                </div>
                                            </div>
                                        </div>
                                        <div className={s.item}>
                                            <div className={s.selectTitle}>3.    ?</div>
                                            <div className={s.selectItem}>
                                                <div className={s.selectSubtitle}></div>
                                                <Select className={s.select}>
                                                    <Option value="" hidden></Option>
                                                    <Option value="18-25">18-25</Option>
                                                    <Option value="25-40">25-40</Option>
                                                    <Option value="40-60">40-60</Option>
                                                    <Option value="60+">60+</Option>
                                                </Select>
                                            </div>
                                        </div>
                                        <div className={s.item}>
                                            <div className={s.selectTitle}>4.     ,     ?</div>
                                            <div className={s.selectesWrap}>
                                                <div className={s.selectItem}>
                                                    <div className={s.selectSubtitle}></div>
                                                    <Select value={valueChildren} className={s.select}
                                                        onChange={setValueChildren}>
                                                        <Option value="" hidden></Option>
                                                        <Option value=""></Option>
                                                        <Option value=",  ">,  </Option>
                                                        <Option value=""></Option>
                                                    </Select>
                                                </div>
                                                <div className={s.selectItem}>
                                                    {valueChildren !== '' && valueChildren !== '' &&
                                                        <div>
                                                            <div className={s.selectSubtitle}>-</div>
                                                            <Select className={s.select} name="" id="">
                                                                <Option value="" hidden></Option>
                                                                <Option value="1">1</Option>
                                                                <Option value="2">2</Option>
                                                                <Option value="3">3</Option>
                                                                <Option value="3+">3+</Option>
                                                            </Select>
                                                        </div>
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                        <div className={s.item}>
                                            <div className={s.selectTitle}>5.               ?</div>
                                            <div className={s.selectesWrap}>
                                                <div className={s.selectItem}>
                                                    <div className={s.selectSubtitle}></div>

                                                    <Select className={s.select}>
                                                        <Option value="" hidden></Option>
                                                        <Option value="  ">  </Option>
                                                        <Option value="  ">  </Option>
                                                        <Option value="   ">   </Option>
                                                    </Select>
                                                </div>
                                                <div className={s.selectItem}>
                                                    <div className={s.selectSubtitle}>-</div>

                                                    <Select className={s.select}>
                                                        <Option value="" hidden></Option>
                                                        <Option value="1">1</Option>
                                                        <Option value="2">2</Option>
                                                        <Option value="3">3</Option>
                                                        <Option value="4">5</Option>
                                                        <Option value="5+">5+</Option>
                                                    </Select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Panel>
                                <Panel header="" key="2">
                                <div className="item-subtitle">
                                               
                                               !
                                    </div>
                                    <div className={s.itemsQuize}>
                                        <div className={s.checkboxes}>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked1} onChange={() => setChecked1(!checked1)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked1 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked2} onChange={() => setChecked2(!checked2)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked2 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked3} onChange={() => setChecked3(!checked3)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked3 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked4} onChange={() => setChecked4(!checked4)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked4 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked5} onChange={() => setChecked5(!checked5)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked5 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked6} onChange={() => setChecked6(!checked6)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked6 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked7} onChange={() => setChecked7(!checked7)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked7 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className={s.checkboxWrap}>
                                                <div className={s.checkboxItem}>
                                                    <Checkbox value={checked8} onChange={() => setChecked8(!checked8)}>
                                                        
                                                    </Checkbox>
                                                </div>
                                                <div className={checked8 ? 'description active' : 'description'}>
                                                    <button>
                                                        <img src="img/edit.svg" alt="" />
                                                        <span> </span>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>

                                        <div className={s.userText}>
                                            <div className={s.title}> </div>
                                            <textarea placeholder=' ...' name="" id="" cols="30" rows="10"></textarea>
                                        </div>
                                    </div>
                                </Panel>
                                <Panel header=" " key="3">
                                <div className="item-subtitle">
                                               
                                               !
                                    </div>
                                    <div className={s.upLoadWrap}>
                                        <Dragger {...props}>
                                            <p className="ant-upload-hint">
                                                     pdf 
                                            </p>
                                            <p className="ant-upload-text">
                                                <img src="img/upLoad.svg" alt="" />
                                                   </p>
                                        </Dragger>

                                    </div>
                                    <Checkbox style={{ marginBottom: '4rem' }}>
                                            ,    
                                    </Checkbox>
                                </Panel>
                            </Collapse>
                            <div className="buttons">
                                {/* <button class="MyBtn_myBtn__nNQdk"></button> */}
                                <button type="button" onClick={(handleCancel)} class="ant-btn ant-btn-primary RequestSteps_btnDark__3gAJB"><span></span></button>
                            </div>
                        </div>
                    </Modal>
                </div>
            </div>
        </div>
    );
};

export default Waiting;